@import "@/assets/global.scss";










































.item-panel {
  width: 100%;
  padding: 18px 12px 18px 14px;
  background: #FFFFFF;
  box-sizing: border-box;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  display: flex;
}

.txtContent {
  position: relative;
  width: 100%;
  margin-left: 8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  > div {
    text-align: left;
  }
}

.tags-panel {
  display: flex;
  position: absolute;
  bottom: 0;
}

.tags {
  margin-right: 6px;
  padding: 3px 10px;
  background: #FFD5C2;
  border-radius: 100px;
}
